body {
  overflow-x: hidden;
}
.blocks {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.block {
  padding: 5px;
  width: 50%;
  box-sizing: border-box;
}
.b--two {
  border-left: 1px solid #e0e0e0;
}
.b--three {
  border-top: 1px solid #e0e0e0;
}
.b--four {
  border-top: 1px solid #e0e0e0;
  border-left: 1px solid #e0e0e0;
}
.-bg-homebuyer-hero {
  background-image: url("../../assets/hero.jpg");
  background-color: #2a1816;
  background-image: -webkit-image-set(url("../../assets/hero.jpg") 1x, url("../../assets/hero-2x.jpg") 2x);
}
.hero-sticky-header--active {
  box-shadow: 0px 0px 2px 2px rgba(0,0,0,0.15);
  background-image: url("../../assets/hero.jpg");
  background-image: -webkit-image-set(url("../../assets/hero.jpg") 1x, url("../../assets/hero-2x.jpg") 2x);
}
.content {
  background-color: #fff;
}
.sectiontitle {
  display: inline-block;
  color: #ab6438;
}
.sectionsubtitle {
  display: inline-block;
  font-size: 12px;
}
.stats {
  margin-top: 10px;
  margin-bottom: 10px;
}
.stat {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-right: 1px solid #e0e0e0;
  height: 90px;
  width: 50%;
  box-sizing: border-box;
}
.statnum {
  display: inline-block;
  font-size: 24px;
  margin-right: 6px;
}
.statdesc {
  display: inline-block;
  font-size: 10px;
}
.stat.last {
  border-right: none;
}
.stat.top {
  border-bottom: 1px solid #e0e0e0;
}
/* Chart styles */
text {
  font-family: Source Code Pro;
  font-size: 10px !important;
  color: #262626;
}
.mdbar rect {
  fill: #d6602c;
  shape-rendering: crispEdges;
}
.mabar rect {
  fill: #54bc4f;
  shape-rendering: crispEdges;
}
.mdbar text,
.mabar text {
  fill: #fff;
  font-size: 10px;
}
.interactive {
  opacity: 0.6;
}
.selected {
  opacity: 1;
}
.y.axis {
  font-size: 10px;
}
.x.axis {
  font-size: 10px;
}
.axis path,
.axis line {
  fill: none;
  stroke: #000;
  shape-rendering: crispEdges;
}
button.tingle-modal__close {
  border: none;
  background: none;
}
@media (max-width: 700px) {
  .statnum {
    font-size: 20px;
  }
}
@media (max-width: 480px) {
  .content {
    padding: 0px;
  }
  .blocks {
    min-width: 230px;
  }
  .block {
    width: 240px;
    height: 260px;
  }
  .hero {
    display: none;
  }
  .hero-sticky-header {
    display: none;
  }
  .help {
    display: none;
  }
  .sectiontitle {
    font-size: 16px;
  }
  .sectionsubtitle {
    font-size: 10px;
  }
  .statnum {
    font-size: 16px;
  }
  .statdesc {
    font-size: 8px;
  }
  .y.axis {
    font-size: 8px;
  }
  .x.axis {
    font-size: 8px;
  }
  .legend {
    font-size: 8px;
  }
  text {
    font-size: 8px !important;
  }
  .mdbar text,
  .mabar text {
    fill: #fff;
    font-size: 8px;
  }
}
@media (max-width: 230px) {
  .blocks {
    min-width: 220px;
  }
  .block {
    width: 100%;
    height: 230px;
  }
  .b--two {
    border: none;
    border-top: 1px solid #e0e0e0;
  }
  .b--three {
    border: none;
    border-top: 1px solid #e0e0e0;
  }
  .b--four {
    border: none;
    border-top: 1px solid #e0e0e0;
  }
}
@media (max-height: 230px) {
  body {
    overflow-y: hidden;
  }
  .block {
    height: 100%;
  }
  .stat {
    height: 85px;
  }
}
